@import '../theme.module.scss';

.social {

    background-image: url("../../media/starbackground.jpg");
    background-size: cover;
    height: calc(100vh - 80px);
    position: relative;

    .aboutMe {
        color: white;
        width: 80%;
        margin: 0 auto;
        text-align: center;
        height: fit-content;
    }

    .aboutMe h1 {
        @include headerStyle;
    }

    p {
        font-style: italic;
        font-size: 18px;
        margin-top: 13px;
    }

    img {
        margin-top: 30px;
        border-radius: 10px;
        margin-bottom: 30px;
        display: block;
        margin-left: auto;
        margin-right: auto;
    }
    
    a {
        margin-top: 10px;
        display: block;
        width: fit-content;
    }

    .some h1 {
        margin-top: 80px;
        color: rgb(223, 187, 240);
        font-size: 30px;
        text-align: center;
        text-shadow: none;
        text-decoration: underline;
    }

    .someLinks {
        display: flex;
        flex-direction: row;
        justify-content: center;
        margin-top: 20px;

        a {
            font-size: 25px;
            margin: 0 8px;
        }

        a:link {
            text-decoration: none;
            color: rgb(223, 187, 240);
        }
          
        a:visited {
            text-decoration: none;
            color: rgb(223, 187, 240);
        }
          
        a:hover {
            text-decoration: underline;
            color: rgb(255, 255, 255);
        }
          
        a:active {
            text-decoration: underline;
            color: rgb(223, 187, 240);
        }

        @media (max-width: 767px) {
            .someLinks {
              flex-wrap: wrap; /* Allow wrapping on smaller screens */
              justify-content: flex-start; /* Align links to the left on smaller screens */
        
              a {
                margin: 0 10px 10px 10px; /* Adjust margin for spacing */
              }
            }
        }
    }
}